import { createCustomElement } from '@servicenow/ui-core';
import { default as iconSet } from '../icons';
import styles from './_sn-uib-icon.scss';
import { resolveIconName } from './utils';

const view = (state) => {
	const { properties: { icon } } = state;
	const resolvedIconName = resolveIconName(icon);

	const iconKey = resolvedIconName && iconSet[resolvedIconName]
		? resolvedIconName
		: 'generic';

	return (
		<img
			className={`sn-uib-icon--${iconKey}`}
			src={iconSet[iconKey]} />
	);
};

createCustomElement('sn-uib-icon', {
	view,
	styles,
	properties: {
		icon: {
			default: null
		}
	}
});
