const NDS_OUTLINE_SUFFIX = '-outline';
const NDS_FILL_SUFFIX = '-fill';

export const resolveIconName = (iconName) => {
	if (!iconName || typeof iconName !== 'string') {
		return iconName;
	}

	const patternString = `${NDS_OUTLINE_SUFFIX}|${NDS_FILL_SUFFIX}`;
	const matchedValues = iconName.match(patternString);

	if (matchedValues) {
		return iconName.slice(0, matchedValues.index);
	} else {
		return iconName;
	}
};
